import { ElementRef } from "@angular/core";
import { MatDialog } from "@angular/material/dialog";
import { MatSnackBar } from "@angular/material/snack-bar";
import { GlobalService } from "../../../../core/services/global.service";
import { ConfirmDialogComponent } from "../../../../shared/components/molecules/confirm-dialog/confirm-dialog.component";
import { UploadDialogComponent } from "../../components/organisms/dialog/upload-dialog/upload-dialog.component";
import { IFile } from "../../interfaces/file/file.interface";
import { map, of, switchMap } from "rxjs";
import { CategoryService, ICategoryData } from "../../services/category.service";
import { FileShared } from "./file.shared";

export class FileRest extends FileShared{
    // categoryData = {//object는 REF를 받으니까. 이건 그냥 기본값만 셋팅해놓은 것이다.
    //     id: 0,
    //     idPath: '',
    //     docType: ''
    // };

    constructor(globalService: GlobalService, private _snackBar: MatSnackBar, dialog: MatDialog, protected categoryService: CategoryService) {
        super(globalService, dialog);
    }
    getFiles(categoryId: number) {
        return this.globalService.dalService.fileHttp!.get({
            categoryId: categoryId
        }).pipe(map(d => {
            return d.data;
        }));
    }


    uploadSuccess(fileList: FileList, fileBrowser: HTMLInputElement){
        this.upload(fileList, this.categoryService.categoryData).subscribe(d=>{
            fileBrowser.value = '';
            if(!d) return;
            this.getFiles(this.categoryService.categoryData.id).subscribe(d =>{
                this.categoryService.files = d;
            })
        })
    }


    //TODO 나중에 얘네들도 컴포넌트가 담당해야 한닥. 여기가 아니라!!!
    deleteFile(file: IFile): void {
        console.log('deleteFile', file.fileId);

        const dialogRef = this.dialog.open(ConfirmDialogComponent, {
            data: {
                title: '파일 삭제',
                content: `${file.fileName}을 삭제하겠습니까?`,
                btnYes: '예',
                btnNo: '아니요',
            }
        });
        dialogRef.afterClosed().subscribe((d: boolean) => {
            if (!d) return;
            console.log('idPath', this.categoryService.categoryData.idPath);
            
            this.globalService.dalService.fileHttp!.delete({
                fileIds: [file.fileId],
                categoryPath: this.categoryService.categoryData.idPath
            }).subscribe(d => {
                if (!d.isSuccess) {
                    this._snackBar.open(d.message, '확인', {
                        horizontalPosition: 'center',
                        verticalPosition: 'top',
                    });
                    return;
                }
                this._snackBar.open('파일 삭제가 완료됐습니다.', '확인', {
                    horizontalPosition: 'center',
                    verticalPosition: 'top',
                });
                this.getFiles(this.categoryService.categoryData.id).subscribe(d =>{
                    this.categoryService.files = d;
                })
                //TODO 다시 get api 쏠지 그냥 프론트만 업데이트 할지
            });
        });
    }

    download(file: IFile): void {
        console.log('download data', file);

        //근데 그러면 /localhost:4200/download/9이런 url이 없는데 왜 success가 뜬걸까?
        this.globalService.dalService.fileHttp?.download(file.fileId).subscribe((response) => {
            console.log('download response', response.headers.get('content-type'));
            const blob = new Blob([response.body!], { type: response.headers.get('content-type')! });
            const linkEl = document.createElement('a');
            linkEl.style.display = 'none';
            const url = window.URL.createObjectURL(blob);//브라우저가 접근할 수 있게 object를 url로 변환한다.
            linkEl.href = url;
            linkEl.download = `${file.fileName}`; // 파일 이름 및 확장자를 지정합니다.
            document.body.appendChild(linkEl);
            linkEl.click();
            document.body.removeChild(linkEl);
            window.URL.revokeObjectURL(url);//이거 안하면 메모리에 남아있음
        });
    }

    currentFileId: number = -1;

}