import { IMenu, IMenuData } from "../../../../components/menu-item/menu/menu.interface";
import { TreerowComponent } from "../../../../components/treeview/treerow/treerow/treerow.component";
import { ICategoryInfo } from "../../interfaces/category/category.interface";
import { CategoryRest } from "../rest/category.rest";

export class CategoryMenuData implements IMenu{

    constructor(private restApi: CategoryRest) {}
    
    menuData: Array<IMenuData> = [
        {
            itemName: '추가',
            icon: 'add',
            itemFunc: this.restApi.createCategory.bind(this.restApi),
            checkDisabled(context: TreerowComponent<ICategoryInfo>){
                return context.data.depth! < 2   ? false : true;
            }
        },
        {
            itemName: '삭제',
            icon: 'delete',
            itemFunc: this.restApi.deleteCategory.bind(this.restApi),
            checkDisabled(context: TreerowComponent<ICategoryInfo>){
                return context.data.depth! > 0 && context.data.children.length ===0  ? false : true;
            }
        },
        {
            itemName: '이름 변경',
            icon: 'edit',
            itemFunc: this.restApi.patchCategoryName.bind(this.restApi),
            checkDisabled(context: TreerowComponent<ICategoryInfo>){
                return false;
            }
        },
    ];

}