import { switchMap, of } from "rxjs";
import { GlobalService } from "../../../../core/services/global.service";
import { UploadDialogComponent } from "../../components/organisms/dialog/upload-dialog/upload-dialog.component";
import { ICategoryData } from "../../services/category.service";
import { MatDialog } from "@angular/material/dialog";

export class FileShared{
    constructor(protected globalService: GlobalService, protected dialog: MatDialog){}
    
    upload(fileList: FileList, categoryData: ICategoryData){
        const dialogRef = this.dialog.open(UploadDialogComponent, {
            data: {
                title: '파일 업로드',
                content: `파일을 학습하는 중입니다.`,
                isBtnDisabled: true,
                isDisableClose: true,
            }
        });
        
        return this.globalService.socketService.fileSocket.connect().pipe(
            switchMap(socketId => {
                const subscription = this.globalService.socketService.fileSocket.onUploadSteps().subscribe((d) => {
                    const gap = 100 / d.maxStep;
                    dialogRef.componentInstance.content = d.message;
                    dialogRef.componentInstance.value = gap * d.step;
                });

                console.log('업로드 시 idPath', categoryData.idPath);
                
                return this.globalService.dalService.fileHttp.upload(
                    {
                        files: fileList,
                        categoryId: categoryData.id,
                        path: categoryData.idPath,
                        socketId: socketId,
                        improvedType: categoryData.docType
                    }).pipe(
                        switchMap(d => {
                            subscription.unsubscribe();
                            this.globalService.socketService.fileSocket.disconnect();
                            dialogRef.componentInstance.btnNo = '닫기';
                            dialogRef.componentInstance.isBtnDisabled = false;
                            if(!d.isSuccess){
                                dialogRef.componentInstance.content = `파일 학습이 실패했습니다. ${d.message}`
                                return of(false);
                            }
                            dialogRef.componentInstance.content = '파일 학습이 완료되었습니다.'
                            return of(true);
                        })
                    );
            })
        )
    }
}