import { Component, Inject } from '@angular/core';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
export interface IUploadDialog {
  title: string;
  content: string;
  btnYes: string;
  btnNo: string;
  isBtnDisabled: boolean;
  isDisableClose: boolean;
}
@Component({
  selector: 'app-upload-dialog',
  templateUrl: './upload-dialog.component.html',
  styleUrl: './upload-dialog.component.scss'
})
export class UploadDialogComponent {
  title: string;
  content: string;
  btnYes: string;
  btnNo: string;
  isBtnDisabled:boolean;
  value: number = 0;
  //isDisableClose: boolean;

  constructor(
    public dialogRef: MatDialogRef<UploadDialogComponent>, 
    @Inject(MAT_DIALOG_DATA) public data: IUploadDialog
  ){
    this.title = data.title;
    this.content = data.content;
    this.btnYes = data.btnYes;
    this.btnNo = data.btnNo;
    this.isBtnDisabled = data.isBtnDisabled ?? false;
    this.dialogRef.disableClose = data.isDisableClose ?? false;
  }

  onClickNo(): void {
    this.dialogRef.close(false);
  }

  onClickYes(): void{
    this.dialogRef.close(true);
  }
}
