import { Component, EventEmitter, Input, OnDestroy, OnInit, Output } from '@angular/core';
import { GlobalService } from '../../../../../core/services/global.service';
import { MatDialog } from '@angular/material/dialog';
import { MatSnackBar } from '@angular/material/snack-bar';
import { IMenu } from '../../../../../components/menu-item/menu/menu.interface';
import { CategoryMenuData } from '../../../classes/menu/category.menu';
import { MatDrawer } from '@angular/material/sidenav';
import { User } from '../../../../membership/interfaces/user.interface';
import { TreeviewComponent } from '../../../../../components/treeview/treeview/treeview.component';
import { ICategoryInfo } from '../../../interfaces/category/category.interface';
import { CategoryRest } from '../../../classes/rest/category.rest';
import { TreeService } from '../../../../../components/treeview/service/tree.service';
import { CategoryCode } from '../../../const/catgegory-root-code.const';

export interface ICategoryClickedOut{
  id: number;
  categoryNamePath: string;
  categoryIdPath: string;
}

@Component({
  selector: 'app-category',
  templateUrl: './category.component.html',
  styleUrl: './category.component.scss'
})
export class CategoryComponent implements OnInit, OnDestroy{
  @Output() categoryClicked = new EventEmitter<ICategoryClickedOut>();
  Object = Object;
  drawerRef!: MatDrawer;
  users: Array<User> = [];//유저들 리스트
  categoryRest:CategoryRest;
  categoryMenu: IMenu;
  categories: Array<ICategoryInfo> = [];
  currentCategoryId!: number;
  categoryCode = new CategoryCode();

  constructor(protected globalService: GlobalService, protected dialog: MatDialog, protected _snackBar: MatSnackBar) {
    this.categoryRest = new CategoryRest(dialog, _snackBar, globalService);
    this.categoryMenu = new CategoryMenuData(this.categoryRest)
  }

  ngOnDestroy(): void {
  }

  ngOnInit(): void {
    const user = this.globalService.currentUser.userData;
    if(!user.id) return;

    this.categoryRest.getCategories({type: this.categoryCode.personal, id: user.id}).subscribe(d => {
      this.categories = d;
    });
    this.globalService.dalService.userHttp.getUsers().subscribe(d =>{
      this.users = d.data;
    })
  }

  onTabChange(event: any){
    console.log('event', event,);
    if(this.categoryCode.codes[event.index].key === 'adminPersonal'){
      this.categories = [];
      return;
    }
    this.categoryRest.getCategories({type: this.categoryCode.codes[event.index].key, id: this.globalService.currentUser.userData.id}).subscribe(d => {
      this.categories = d;
    });
  }

  onCategoryClick(categoryId: number, treeView: TreeviewComponent<ICategoryInfo>) {
    console.log('treeview에서부터 올라온 이벤트', categoryId);
    const categoryIdPath = treeView.treeService.findPath(categoryId);
    console.log('categoryPath', categoryIdPath);
    this.categoryClicked.emit({
      id: categoryId,
      categoryNamePath: this.setCategoryPath(categoryId, treeView.treeService),
      categoryIdPath: categoryIdPath
    });
  }

  getUserCategory(a: any){
    console.log('aaaa', a, a.source._value);
    this.categoryRest.getCategories({type: this.categoryCode.personal, id: a.source._value}).subscribe(d => {
      this.categories = d;
    });
  }

  setCategoryPath(categoryId: number, treeService: TreeService<ICategoryInfo>) {
    const parent = treeService.findParent(categoryId);
    const me = treeService.findMe(categoryId);
    this.currentCategoryId = categoryId;
    const parentName = parent?.name ?? '';
    const categoryName = parentName ? '> ' + me!.name : me!.name;
    return `${parentName} ${categoryName}`;
  }
}
