import { inject } from "@angular/core";
import { IMenu, IMenuData } from "../../../../components/menu-item/menu/menu.interface";
import { IFileRest } from "../../../../core/http/file.http";
import { FileRest } from "../rest/file.rest";
import { Router } from "@angular/router";

export class FileMenuData implements IMenu{
    constructor(private restApi: FileRest, private router: Router){}
    menuData: Array<IMenuData> = [
        {
            itemName: '삭제',
            icon: 'delete',
            itemFunc: this.restApi.deleteFile.bind(this.restApi),
            checkDisabled(){
                return false;
            }
        },
        {
            itemName: '다운로드',
            icon: 'library_books',
            itemFunc: this.restApi.download.bind(this.restApi),
            checkDisabled(){
                return false;
            }
        },
        {
            itemName: '파일 보기',
            icon: 'library_books',
            itemFunc: ()=>{
                this.router.navigateByUrl('/editor');
            },
            checkDisabled(){
                return false;
            }
        }
    ];
}