import { Component, Inject } from '@angular/core';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { IConfirmDialog } from '../../../../../shared/components/molecules/confirm-dialog/confirm-dialog.component';

@Component({
  selector: 'app-file-upload-dialog',
  templateUrl: './file-upload-dialog.component.html',
  styleUrl: './file-upload-dialog.component.scss'
})
export class FileUploadDialogComponent {
  unselected: File[] = [];
  constructor(dialogRef: MatDialogRef<FileUploadDialogComponent>, 
    @Inject(MAT_DIALOG_DATA) public data: {
      selected: File[]
  }){}

  onUnselect(file: File){
    const index = this.data.selected.indexOf(file);
    this.data.selected.splice(index, 1);
    this.unselected.push(file);
  }

  onSelect(file: File){
    this.unselected = this.unselected.filter(d=> d !== file);
    this.data.selected.push(file);
  }
}
