import { Component, ElementRef, Input, OnInit, ViewChild, inject } from '@angular/core';
import { GlobalService } from '../../../../../core/services/global.service';
import { MatDialog } from '@angular/material/dialog';
import { MatSnackBar } from '@angular/material/snack-bar';
import { FileRest } from '../../../classes/rest/file.rest';
import { FileMenuData } from '../../../classes/menu/file.menu';
import { CategoryService } from '../../../services/category.service';
import { IMenu } from '../../../../../components/menu-item/menu/menu.interface';
import { Router } from '@angular/router';
import { FileUploadDialogComponent } from '../../molecules/file-upload-dialog/file-upload-dialog.component';
interface IDocType{
  code: string;
  name: string;
}

@Component({
  selector: 'app-file',
  templateUrl: './file.component.html',
  styleUrl: './file.component.scss'
})
export class FileComponent implements OnInit{
  @ViewChild('fileBrowser', {static: true}) fileBrowser!:  ElementRef<HTMLInputElement>;
  @ViewChild('folderBrowser', {static: true}) folderBrowser!:  ElementRef<HTMLInputElement>;
  @Input() fileRest: FileRest | null = null;
  fileMenu: IMenu | null = null;
  docTypes: IDocType[] = [
    {code: 'default', name: '기본'},
    {code: 'n_complaint', name: '국민신문고'}];

  router: Router = inject(Router);
  constructor(protected globalService: GlobalService, protected dialog: MatDialog, protected _snackBar: MatSnackBar, protected categoryService: CategoryService) {
    // this.fileRest.categoryData = categoryService.categoryData;
  }

  ngOnInit(): void {
    if(!this.fileRest) return;
    this.fileMenu = new FileMenuData(this.fileRest, this.router);
    this.getDocTypes();
  }
  
  onFilesSelected(event: any, fileBrowser: HTMLInputElement){
    const fileList: FileList = event.target.files;
    const isFileValid = this.checkFileFormat(fileList);
    if(!isFileValid) return;
    if(!this.fileRest) return;
    this.fileRest.uploadSuccess(event.target.files, fileBrowser);
  }

  onFileBrowserClicked() {
    const isCategoryClicked = this.onInputClicked();
    if(!isCategoryClicked) return;
    this.fileBrowser.nativeElement.click();
  }

  onFolderSelected(event: any, folderBrowser: HTMLInputElement){
    console.log('folder testing', event, folderBrowser.value, event.target.files);
    const fileList = event.target.files
    const isFileValid = this.checkFileFormat(fileList);
    if(!isFileValid) return;
    const files: File[] = [];
    for(let i = 0; i < fileList.length; i++){
      files.push(fileList.item(i));
    }
    //console.log('files', files, this.buildFileTree(files));
    folderBrowser.value= '';
    const dialogRef = this.dialog.open(FileUploadDialogComponent, 
      {
        data: {
          selected: files
        }
      });
    dialogRef.afterClosed().subscribe(result=>{
      console.log('result', result, files);
      if(!result) return;
      const dataTransfer = new DataTransfer();
      files.forEach(file => dataTransfer.items.add(file));
      console.log('sdfsdf', dataTransfer.files);
      if(!this.fileRest) return;
      this.fileRest.uploadSuccess(event.target.files, folderBrowser);
    })
  }

  onFolderBrowserClicked() {
    const isCategoryClicked = this.onInputClicked();
    if(!isCategoryClicked) return;
    this.folderBrowser.nativeElement.click();
  }

  getDocTypes(){
    this.globalService.dalService.codeHttp.getCommonCode('document').subscribe(d =>{
      // if(d.data.length === 0){
      //   return;
      // }
      // this.docTypes = d.data;
    })
  }

  // buildFileTree(files: File[]): any {
  //   const tree = {};
  //   files.forEach(file => {
  //     const pathParts = file.webkitRelativePath.split('/');
  //     console.log('pathParts', pathParts);
      
  //     let current: any = tree;
  //     for (let i = 0; i < pathParts.length; i++) {
  //       const part = pathParts[i];
  //       if (!current[part]) {
  //         if (i === pathParts.length - 1) {//파일이면
  //           current[part] = file;//파일이면 파일을 넣는다.
  //         } else {
  //           current[part] = {};//폴더면 폴더를 넣는다.
  //         }
  //       }
  //       // console.log('current', current, 'currentPart', current[part]);
  //       //debugger;
  //       current = current[part];//current를 옮겨줘야 한다. 하위로!
  //     }
  //   });
  //   return tree;
  // }

  onInputClicked(){
    if(!this.categoryService.categoryData.id){
      this._snackBar.open('카테고리를 선택해주세요', '확인', {
        horizontalPosition: 'center',
        verticalPosition: 'top',
      });
      return false;
    }
    return true;
  }

  checkFileFormat(fileList: FileList){
    for(let i = 0; i < fileList.length; i++){
      const type: string = fileList.item(i)!.type;
      console.log('file type', type);
      if(!type.includes('pdf')){
        this._snackBar.open('pdf만 업로드 가능합니다.', '확인', {
          horizontalPosition: 'center',
          verticalPosition: 'top',
        });
        return false;
      }
    }
    return true;
  }
}