import { Component, OnInit } from '@angular/core';
import { StoreAbstract } from '../../../../../core/classes/store.abstract';
import { GlobalService } from '../../../../../core/services/global.service';

@Component({
  selector: 'app-category-manage-template',
  templateUrl: './category-manage-template.component.html',
  styleUrl: './category-manage-template.component.scss'
})
export class CategoryManageTemplateComponent extends StoreAbstract implements OnInit{

  ngOnInit(): void {
    this.OnResizeRedux();
  }

  protected override OnResizeRedux(): void {
    this.resize$.subscribe(d => {
      this.layoutService.leftWidh = d.leftWidth;
      this.layoutService.rightWidth = d.rightWidth;
    })
    this.resize({leftWidth: 50, rightWidth: 50});
  }
}
