import { MatSnackBar } from "@angular/material/snack-bar";
import { GlobalService } from "../../../../core/services/global.service";
import { ICategoryGetRequest, ICategoryMoveRequest } from "../../interfaces/category/category.dto";
import { TreerowComponent } from "../../../../components/treeview/treerow/treerow/treerow.component";
import { ICategoryInfo } from "../../interfaces/category/category.interface";
import { CategoryDialogComponent } from "../../components/organisms/dialog/category-dialog/category-dialog.component";
import { MatDialog } from "@angular/material/dialog";
import { ConfirmDialogComponent } from "../../../../shared/components/molecules/confirm-dialog/confirm-dialog.component";
import { moveItemInArray } from "@angular/cdk/drag-drop";
import { IMoveData } from "../../../../components/treeview/tree.store";
import { TreeviewComponent } from "../../../../components/treeview/treeview/treeview.component";
import { map } from "rxjs";

//TODO 이름도 바꾸기!
export class CategoryRest {

    constructor(
        private dialog: MatDialog,
        private _snackBar: MatSnackBar,
        private globalService: GlobalService,
    ) {}
    
    getCategories(request: ICategoryGetRequest) {
        return this.globalService.dalService.categoryHttp!.get(request).pipe(map(d => {
            return d.data.map(d => {
                const { children, ...rest } = d;
                return {
                    ...rest,
                    children: JSON.parse(d.children)
                }
            });
        }));
    }


    createCategory(context: TreerowComponent<ICategoryInfo>): void {
        const dialogRef = this.dialog.open(CategoryDialogComponent, {
            data: {
                defaultValue: '',
            }
        });
        dialogRef.afterClosed().subscribe(categoryName => {
            if (!categoryName) return;
            const parent = context.data;
            const treeService = context.treeService;
            //내 부모 카테고리를 다 찾아오자!
            const path = treeService.findPath(parent.id);
            console.log('path', path);

            this.globalService.dalService.categoryHttp!.create({
                parentId: parent.id,
                parentDepth: parent.depth!,
                categoryName: categoryName,
                insertName: this.globalService.currentUser.userData.name!,//카테고리 생성자
                type: parent.type,
                insertId: this.globalService.currentUser.userData.id,
                companyCode: '',
                path: path,
            }).subscribe(d => {
                if (!d.isSuccess) {
                    this._snackBar.open(d.message, '확인', {
                        horizontalPosition: 'center',
                        verticalPosition: 'top',
                    });
                    return;
                }
                console.log(d, d.data);
                const found = treeService.findMe(parent.id);
                found!.children.push(d.data.id);  //자식에 받아온 데이터 추가해주기
                console.log('found', found);
                const { steps, children, ...rest } = d.data
                treeService.datasource.push({ ...rest, children: JSON.parse(children) });//이거 대신 그냥 get 해도 되긴 하는데...!
                this._snackBar.open('카테고리가 생성되었습니다.', '확인', {
                    horizontalPosition: 'center',
                    verticalPosition: 'top',
                });
            });
        });
    }

    deleteCategory(context: TreerowComponent<ICategoryInfo>): void {
        const data = context.data;
        const treeService = context.treeService;

        const dialogRef = this.dialog.open(ConfirmDialogComponent, {
            data: {
                title: '카테고리 삭제',
                content: `${data.name}을 삭제하겠습니까? 카테고리 내부의 파일도 함께 삭제됩니다.`,
                btnYes: '예',
                btnNo: '아니요',
            }
        });
        dialogRef.afterClosed().subscribe((d: boolean) => {
            console.log('ddd', d);
            if (!d) return;
            const parent = treeService.findParent(data.id);
            const path = treeService.findPath(data.id);
            console.log('path', path);
            console.log('aaaa', { categoryId: data?.id, parentId: parent?.id, path: path });
            this.globalService.dalService.categoryHttp.delete({ categoryId: data.id, parentId: parent!.id, path: path }).subscribe(d => {
                if (!d.isSuccess) {
                    this._snackBar.open(d.message, '확인', {
                        horizontalPosition: 'center',
                        verticalPosition: 'top',
                    });
                    return;
                }
                console.log('delete id', data.id);
                // console.log('안뇽 나는 deleteCategory', data);
                // //카테고리 삭제
                treeService.datasource = treeService.datasource.filter((d: any) => d.id !== data.id);
                console.log('wow', treeService.datasource);
                const parent = treeService.findParent(data.id);
                const index = parent!.children.indexOf(data.id);
                parent?.children.splice(index, 1);
                //this.resetData(treeService);
                this._snackBar.open('카테고리가 삭제되었습니다.', '확인', {
                    horizontalPosition: 'center',
                    verticalPosition: 'top',
                });
            });
        });
    }

    patchCategoryName(context: TreerowComponent<ICategoryInfo>): void {
        const dialogRef = this.dialog.open(CategoryDialogComponent, {
            data: {
                defaultValue: context.data.name,
            }
        });
        dialogRef.afterClosed().subscribe(categoryName => {
            if (!categoryName) return
            const data = context.data;
            const treeService = context.treeService;
            console.log('name', data.id, categoryName);
            this.globalService.dalService.categoryHttp!.rename({
                categoryId: data.id,
                categoryName: categoryName,
            }).subscribe(d => {
                if (!d.isSuccess) {
                    this._snackBar.open(d.message, '확인', {
                        horizontalPosition: 'center',
                        verticalPosition: 'top',
                    });
                    return;
                }
                const me = treeService.findMe(data.id);
                me!.name = categoryName;
                this._snackBar.open('카테고리 이름이 변경되었습니다.', '확인', {
                    horizontalPosition: 'center',
                    verticalPosition: 'top',
                });
            })
        });
        //console.log('안뇽 나는 patchCategoryName', data);
    }

    patchCategoryMove(categoryInfo: ICategoryInfo, treeView?: TreeviewComponent<ICategoryInfo>) {
        const dialogRef = this.dialog.open(ConfirmDialogComponent, {
            data: {
                title: '카테고리 이동',
                content: `${categoryInfo.name}을 이동하겠습니까?`,
                btnYes: '예',
                btnNo: '아니요',
            }
        });
        dialogRef.afterClosed().subscribe((d: boolean) => {
            if (!d) return;
            console.log('before', JSON.stringify(treeView!.treeService.datasource));
            const request: ICategoryMoveRequest = {
                parentId: categoryInfo.id,
                children: JSON.stringify(categoryInfo.children)
            };

            this.globalService.dalService.categoryHttp!.move(request).subscribe(d => {
                if (!d.isSuccess) {
                    this._snackBar.open(d.message, '확인', {
                        horizontalPosition: 'center',
                        verticalPosition: 'top',
                    });
                    return;
                }
                this._snackBar.open('카테고리가 이동됐습니다.', '확인', {
                    horizontalPosition: 'center',
                    verticalPosition: 'top',
                });
            });//실패하면 request 넣어서 전에 데이터 가져오기
        });
    }

    move(event: IMoveData, treeView: TreeviewComponent<ICategoryInfo>) {
        const parent = treeView.treeService.findMe(event.movedParentId)!;
        const dialogRef = this.dialog.open(ConfirmDialogComponent, {
            data: {
                title: '카테고리 이동',
                content: `${parent.name}을 이동하겠습니까?`,
                btnYes: '예',
                btnNo: '아니요',
            }
        });
        dialogRef.afterClosed().subscribe((d: boolean) => {
            if (!d) {
                moveItemInArray(parent.children, event.movedData.previousIndex, event.movedData.currentIndex);
                return;
            }
            console.log('before', JSON.stringify(treeView!.treeService.datasource));
            const request: ICategoryMoveRequest = {
                parentId: parent.id,
                children: JSON.stringify(parent.children)
            };

            this.globalService.dalService.categoryHttp!.move(request).subscribe(d => {
                if (!d.isSuccess) {
                    this._snackBar.open(d.message, '확인', {
                        horizontalPosition: 'center',
                        verticalPosition: 'top',
                    });
                    return;
                }
                this._snackBar.open('카테고리가 이동됐습니다.', '확인', {
                    horizontalPosition: 'center',
                    verticalPosition: 'top',
                });
            });//실패하면 request 넣어서 전에 데이터 가져오기
        });
    }
}