import { Component, EventEmitter, Input, Output, ViewChild } from '@angular/core';
import { IMenu } from '../../../../../components/menu-item/menu/menu.interface';
import { MenuComponent } from '../../../../../components/menu-item/menu/menu.component';
import { IFile } from '../../../interfaces/file/file.interface';

export interface IHeader{
  name: string;
  data: string;
  type: 'text' | 'button' | 'image' | 'checkbox';
}
@Component({
  selector: 'app-table',
  templateUrl: './table.component.html',
  styleUrl: './table.component.scss'
})
export class TableComponent {
  @Input() headers!: Array<string>;
  @Input() datasource!: Array<IFile>;
  @Input() menuClass: IMenu = {
    menuData: []
  };
  @Output() rowClicked = new EventEmitter<any>;

  @ViewChild('menu', { static: false }) menuComponent!: MenuComponent;
  displayedColumns: string[] = ['fileName', 'fileFormat', 'fileSize', 'insertDate', 'star'];

  openMenu(){
    console.log('clicked', this.menuComponent);
    if(this.menuClass.menuData.length === 0) return;
    this.menuComponent.openMenu();
    this.menuComponent.menu.menuClosed.subscribe(d=>{

    });
  }

  getDate(date: Date){
    const dateObj = new Date(date);
    return `${dateObj.getFullYear()}.${String(dateObj.getMonth()).padStart(2, '0')}.${String(dateObj.getDate()).padStart(2, '0')}`
  }

  getSize(bytes: number){
    return `${Math.ceil(bytes/1024)}KB`;
  }
}
