import { Component, ElementRef, OnDestroy, OnInit, ViewChild } from '@angular/core';
import { GlobalService } from '../../../../../core/services/global.service';
import { MatDialog } from '@angular/material/dialog';
import { MatSnackBar } from '@angular/material/snack-bar';
import { IMenu } from '../../../../../components/menu-item/menu/menu.interface';
import { CategoryMenuData } from '../../../classes/menu/category.menu';
import { FileMenuData } from '../../../classes/menu/file.menu';
import { TreeviewComponent } from '../../../../../components/treeview/treeview/treeview.component';
import { ICategoryInfo } from '../../../interfaces/category/category.interface';
import { MatDrawer } from '@angular/material/sidenav';
import { User } from '../../../../membership/interfaces/user.interface';
import { ICategoryClickedOut } from '../../organisms/category/category.component';
import { CategoryRest } from '../../../classes/rest/category.rest';
import { FileRest } from '../../../classes/rest/file.rest';
import { IFile } from '../../../interfaces/file/file.interface';
import { TreeService } from '../../../../../components/treeview/service/tree.service';
import { CategoryService } from '../../../services/category.service';

@Component({
  selector: 'app-category-manage-page',
  templateUrl: './category-manage-page.component.html',
  styleUrl: './category-manage-page.component.scss'
})
export class CategoryManagePageComponent implements OnInit{
  @ViewChild('fileBrowser', {static: true}) fileBrowser!:  ElementRef<HTMLInputElement>;
  users: Array<User> = [];
  fileRest: FileRest;

  constructor(protected globalService: GlobalService, protected dialog: MatDialog, protected _snackBar: MatSnackBar, protected categoryService: CategoryService) {
    this.fileRest = new FileRest(globalService, _snackBar, dialog, categoryService);
  }

  ngOnInit(): void {
    const user = this.globalService.currentUser.userData;
    if(!user.id) return;
    this.globalService.dalService.userHttp.getUsers().subscribe(d =>{
      this.users = d.data;
    })
  }


  onCategoryClicked(event: ICategoryClickedOut){
    if(!event.categoryIdPath){
      alert('카테고리 경로를 찾지 못했습니다.');
      return;
    }

    this.categoryService.categoryData.namePath = event.categoryNamePath;
    this.categoryService.files = [];
    this.categoryService.categoryData.idPath = event.categoryIdPath;
    this.categoryService.categoryData.id = event.id;

    this.fileRest.getFiles(event.id).subscribe(d =>{
      this.categoryService.files = d;
    });
  }
}
